@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Open Sans", serif;
}

.nav-bar {
  background-color: #dc4438;
  border-bottom: 2px solid black;
  border-top: 1px solid black;
  height: 46px;
}

.nav-bar > ul {
  display: flex;
  flex-direction: row;
  padding: 10px 0px;
  list-style: none;
}

.home {
  flex-grow: 1;
}

.nav-link > a {
  font-family: "Russo One", sans-serif;
  color: white;
  text-decoration: none;
  margin: 0px 25px;
  font-size: 20px;
}

.nav-link > a:hover {
  color: rgb(200, 200, 200);
}

.cart-icon {
  margin-right: 10px;
  fill: white;
}

.cart-count {
  position: absolute;
  top: 25px;
  right: 20px;
  background-color: white;
  border: 1px solid black;
  border-radius: 50px;
  color: black;
  padding: 0px 5px;
  font-size: 12px;
}

.home-page {
  background-color: #555;
}

.home-page > img {
  position: relative;
  width: 100%;
  height: calc(100vh - 54px);
  object-fit: cover;
}

.overlay {
  position: absolute;
  top: 15%;
  left: 50%;
  width: 80%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.store-name {
  font-family: "Russo One", sans-serif;
  text-align: center;
  text-shadow: 2px 2px 5px white;
  margin: auto;
  font-size: 70px;
}

.text-section {
  font-family: "Russo One", sans-serif;
  color: black;
  text-shadow: rgba(255, 255, 255, 0.5) 0px 0px 10px;
  margin: 0px auto;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}

.text-section > h2 {
  margin: auto;
  text-align: center;
  font-size: 32px;
  text-overflow: ellipsis;
}

.browse-button {
  font-family: "Open Sans", serif;
  width: 120px;
  padding: 5px 10px;
  background-color: #dc4438;
  font-size: 20px;
  color: white;
  border: 1px solid black;
  border-radius: 5px;
  text-decoration: none;

  text-align: center;
  margin: 5px auto;
}

@media (min-width: 400px) {
  .overlay {
    top: 25%;
  }
}

@media (min-width: 650px) {
  .text-section {
    width: 600px;
  }
}

@media (min-width: 960px) {
  .text-section {
    margin-top: 50px;
  }
}

.products-page {
  background-color: rgb(230, 230, 230);
  height: 100%;
}

.product-section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 90%;
  margin: auto;
}

.products-page > h1 {
  font-family: "Russo One", sans-serif;
  text-align: center;
  font-size: 40px;
  padding: 5px;
}

@media (min-width: 1787px) {
  .products-page {
    height: 100vh;
  }
}

.product-card {
  background-color: white;
  margin: auto;
  margin-bottom: 10px;
  border: 1px solid gray;
  border-radius: 10px;
  width: 400px;
  text-align: center;
}

.tool-image {
  display: block;
  margin: 10px auto;
  width: 200px;
  height: 200px;
  object-fit: scale-down;
}

.tool-name {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid gray;
}

.description {
  font-size: 14px;
  padding: 0px 25px;
}

.price {
  font-size: 32px;
  font-weight: 600;
}

.bottom-section {
  display: flex;
  justify-content: space-evenly;
  padding: 10px;
}

.buy-button {
  padding: 0px 10px;
  background-color: #dc4438;
  color: white;
  border-radius: 5px;
}

.shopping-cart {
  position: absolute;
  background-color: rgb(230, 230, 230);
  z-index: 1;
  top: 50px;
  right: 0;
  /* width: 350px; */
  width: 80%;
  max-width: 350px;
  min-height: 250px;
  border-radius: 10px;
  padding: 10px;
}

.cart-items {
  max-height: 500px;
  overflow-y: scroll;
  background-color: white;
  border-radius: 10px;
  margin-bottom: 10px;
}

.order-total {
  text-align: center;
  font-size: 26px;
}

.nav-bar {
  z-index: 1;
}

.close-cart {
  position: fixed;
  top: 0px;
  background-color: rgba(0, 0, 0, 0.75);
  height: 100vh;
  width: 100%;
}

.cart-heading {
  font-family: "Russo One", sans-serif;
  text-align: center;
  font-size: 30px;
}

.cart-bottom > button {
  padding: 5px 10px;
  background-color: #dc4438;
  font-size: 20px;
  color: white;
  width: 125px;
  border-radius: 5px;
}

.cart-total {
  font-size: 30px;
}

.cart-card {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 5px auto;
}

.cart-image > img {
  width: 100px;
  height: 100px;
  object-fit: scale-down;
  padding: 0px 20px;
}

.cart-info {
  flex-grow: 1;
  text-align: center;
}

.quantity-control {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.quantity-control > button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15px;
  width: 15px;
  background-color: #dc4438;
  color: white;
  border-radius: 5px;
  padding: 6px;
}

.quantity-control > p {
  padding: 3px;
}

